import mion from './mion_management.png';
import styles from './styles.module.css';
import { Button } from '@consta/uikit/Button';
import { IconAdd } from '@consta/uikit/IconAdd';
import { Text } from '@consta/uikit/Text';
import { IconYoutube } from '@consta/uikit/IconYoutube';
import { IconStorage } from '@consta/uikit/IconStorage';
import mionLogoBlack from './mion_logo_black.svg';
import mionLogoWhite from './mion_logo_white.svg';
import { IconChatFilled } from '@consta/icons/IconChatFilled';
import { IconWatchFilled } from '@consta/icons/IconWatchFilled';
import { IconFavoriteFilled } from '@consta/icons/IconFavoriteFilled';
import { IconPlay } from '@consta/icons/IconPlay';
import { List } from '@consta/uikit/ListCanary';

const items = ['Новый чат', 'История запросов', 'Избранное', 'Музыка'];

export const Chats = () => (
  <>
    <div className={styles.container}>
      {/* <img src={mion} className={styles.logo}></img> */}
      <img src={mionLogoWhite} className={styles.logo}></img>
      <div className={styles.chatsItems}>
        <Button
          label="Новый чат"
          view="clear"
          iconLeft={IconChatFilled}
          size="l"
          className={styles.chatItemBtn}
        />
        <Button
          label="История запросов"
          view="clear"
          size="l"
          iconLeft={IconWatchFilled}
          className={styles.chatItemBtn}
        ></Button>
        <Button
          label="Избранное"
          view="clear"
          size="l"
          iconLeft={IconFavoriteFilled}
          className={styles.chatItemBtn}
        ></Button>
        <Button
          label="Музыка"
          view="clear"
          size="l"
          iconLeft={IconPlay}
          className={styles.chatItemBtn}
        ></Button>
      </div>
      {/* <div className={styles.chatsItems}>        
        <div className={styles.chatsItem}>
          <IconStorage className={styles.icons} />
          <Text>История запросов</Text>
        </div>
        <div className={styles.chatsItem}>
          <IconFavorite className={styles.icons} />
          <Text>Избранное</Text>
        </div>
        <div className={styles.chatsItem}>
          <IconYoutube className={styles.icons} />
          <Text>Музыка</Text>
        </div>
      </div> */}
    </div>
  </>
);
