import { Layout } from '@consta/uikit/Layout';
import { Text } from '@consta/uikit/Text';
import styles from './styles.module.css';
import { Menu } from './Menu/Menu';
import { Chat } from './Chat/Chat';
import { Chats } from './Chats/Chats';
import { useState } from 'react';

export const MainPage = ({ onClick, innerRef }) => {
  const [visibility, setVisibility] = useState(false);

  function hideBlock() {
    if (!visibility) return { display: 'none' };
    else return { display: 'block' };
  }

  return (
    <div style={hideBlock()} ref={innerRef}>
      <Layout direction="row" className={styles.mainLayout}>
        <Layout direction="column" className={styles.sideBar}>
          <Layout flex={1}>
            {/* <div className={styles.chats}> */}
            <Chats />
            {/* </div> */}
          </Layout>
          <Layout flex={1}>
            <Menu onClick={onClick} />
          </Layout>
        </Layout>
        <Layout flex={3} className={styles.chats}>
          <Chat />
        </Layout>
        <div className={styles.rightBarrier}></div>
      </Layout>
    </div>
  );
};
